
.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    background-color: #111;
}

.logo{
    position: fixed;
    left: 20px;
    width: 80px;
}

.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
}

*{
    margin: 0;
    padding: 0;
    
}
body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #111;
}
.banner{
    background-image: url('https://wallpaperaccess.com/full/2703652.png');
    background-size: cover;
    height: 428px;
    color: white;
   
}
.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 12px;
}
titel{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom:0.3rem;
}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    margin-right: 1rem;
    border-radius: 3px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom:0.5rem;
    background-color: rgba(51, 51, 51,.5 );
    cursor: pointer;
}
.button:hover{
    color: black;
    background-color: #e6e6e6;
}
.discription{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}
.fade-bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}

.row{
    margin-left: 20px;
    color: white;
}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.posters::-webkit-scrollbar{
    display: none;
}
.poster{
    cursor: pointer;
    max-height: 250px;
    margin-right: 10px;
}
.smallposter{
    cursor: pointer;
    max-height: 150px;
    max-width: 100%;
    margin-right: 10px;
}
.poster:hover{ /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(1.2); 
}

